.content {
    position: absolute;
    top: 252px;
    width: 100%;
    height: 35px;
    padding: 0 30px;
    text-align: center;
    overflow: hidden;
    transition: 0.5s;
}

.container .card:hover .content {
    top: 130px;
    height: 250px;
}

.container .card .content .h2 {
    font-size: 1.5rem;
    font-weight: 700;
    color: #009688;
    /* color: var(--color,#009688); */
}

.container .card .content .p {
    color: #333;
}

.container .card .content .a {
    position: relative;
    top: 15px;
    display: inline-block;
    padding: 12px 25px;
    text-decoration: none;
    background: #009688;
    /* background: var(--color,#009688); */
    transition: 0.5s;
    color: white;
    font-weight: 500;
   
}

.container .card .content .a:hover {
    opacity: 0.8;
    background: #2b25dd;
    border-radius: 10px;
    box-shadow: 10px 5px 5px rgb(99, 97, 97);
}

@media (max-width: 480px) {
    .container .card {
        width: 230px;
        border-radius: 15px;
    }

    .container .card .img-box {
        width: 185px;
        border-radius: 10px;
    }

    .container .card .content .p {
        font-size: 0.8rem;
    }

    .container .card .content .a {
        font-size: 0.9rem;
    }
}